export interface ChatTheme {
    header: string;
    optionButton: string;
    textBackground: string;
}

export const getBrandChatTheme = (brandId: string): ChatTheme => {
    return {
        header: '#000',
        optionButton: '#000',
        textBackground: '#f7f4ec'
    };
};
export const isJsonString = (str: string) => {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}