import { format } from 'date-fns';
import React from 'react';
import { ChatMessageType, IChatMessageBody } from '../ChatConnectionContext';
import PlainTextChatBubble from './PlainTextChatBubble';
import ProductChatBubble from './ProductChatBubble';
import MeetingLinkChatBubble from './MeetingLinkChatBubble';
import StoryChatBubble from './StoryChatBubble';
import { ClientChatContext } from '../LiveChatPanel/ClientChatContext';

const ChatBubbleContainer = ({
  messageBody,
  left,
  message,
  messageString,
  onClickProduct,
  clientSide = false
}: {
  messageBody: IChatMessageBody;
  messageString: string;
  left: boolean;
  message: any;
  onClickProduct: (productId: string) => void;
  clientSide?: boolean;
}) => {
  const timestamp = messageBody?.timestamp || message?.createdAt || null;
  let chatContent = <PlainTextChatBubble left={left} text={messageString} />;
  const { storeId } = React.useContext(ClientChatContext);

  //   const openVBPopup = (payload: any) => {
  //     if (payload?.action) {
  //       dispatch(payload.action);
  //     } else {
  //       const pageViewType = payload?.type;
  //       const idByLang = payload?.id || {};
  //       const id = idByLang[i18n.language] || payload?.id;
  //       const action = getPopupAction(
  //         `${location.protocol}//${location.host}`,
  //         pageViewType,
  //         id,
  //         payload?.brandId,
  //         storeId
  //       );
  //       console.log(action);
  //       if (action) {
  //         dispatch(action);
  //       }
  //     }
  //     logEvent(DID_OPEN_VB_CONTENT_POPUP, DID_OPEN_VB_CONTENT_POPUP, payload);
  //   };

  if (messageBody) {
    switch (messageBody?.type) {
      case ChatMessageType.PLAIN_TEXT:
        chatContent = (
          <PlainTextChatBubble left={left} text={messageBody?.content} />
        );
        break;
      case ChatMessageType.PRODUCT:
        chatContent = (
          <ProductChatBubble
            left={left}
            id={messageBody?.content}
            onClickProduct={onClickProduct}
          />
        );
        break;
      case ChatMessageType.STORY:
        chatContent = (
          <StoryChatBubble
            left={left}
            id={messageBody?.content}
            // openVBPopup={openVBPopup}
          />
        );
        break;
      case ChatMessageType.MEETING_LINK:
        chatContent = (
          <MeetingLinkChatBubble
            left={left}
            link={messageBody?.content}
            disabled={!clientSide}
          />
        );
        break;
      default:
        chatContent = <PlainTextChatBubble left={left} text={messageString} />;
        break;
    }
  }
  return (
    <div className={`message-item ${left && `left`}`}>
      {chatContent}
      {timestamp && (
        <div className={`message-timestamp ${left && `left`}`}>
          {left ? `Received` : `Sent`}{' '}
          {format(new Date(timestamp), 'd LLL uuuu, hh:mm aaa')}
        </div>
      )}
    </div>
  );
};

export default ChatBubbleContainer;
