import ChatConnectionContextContainer from './components/ChatConnectionContext';
import ClientChatContextContainer from './components/LiveChatPanel/ClientChatContext';
import React from 'react';
import liveChatConfig from './components/LiveChatPanel/liveChatConfig';
import LiveChatPanel from './components/LiveChatPanel/LiveChatPanel';
import { setNewIdentityIfNeeded } from './utils/identityId';

const App = ({ storeId, brandId }: { storeId: string; brandId: string }) => {
  React.useEffect(() => {
    setNewIdentityIfNeeded();
  }, []);
  return (
    <>
      <div className="live-chat-container">
        <ChatConnectionContextContainer>
          <ClientChatContextContainer storeId={storeId}>
            <LiveChatPanel config={liveChatConfig} brandId={brandId} />
          </ClientChatContextContainer>
        </ChatConnectionContextContainer>
      </div>
      <style jsx>
        {`
          .message-list {
            height: 44vh;
            border-bottom: 1px solid #c4c4c4;
            padding: 18px 20px;
            overflow: auto;
          }
        `}
      </style>
    </>
  );
};

export default App;
