import React from 'react';
import ReactDOM from 'react-dom';
import App from './src/App';
import * as fs from 'fs';

const styleCssString = fs.readFileSync(__dirname + '/src/style.css');
const bootstrapCssString = fs.readFileSync('node_modules/bootstrap/dist/css/bootstrap.min.css');

function injectCssToJS(cssString) {
  const style = document.createElement('style');
  style.type = 'text/css';
  style.appendChild(document.createTextNode(cssString));
  document.head.appendChild(style);
}

injectCssToJS(styleCssString);
injectCssToJS(bootstrapCssString);

const chatWidgetRoot = document.createElement('div');
chatWidgetRoot.setAttribute('id', 'chat-widget-root');
document.body.appendChild(chatWidgetRoot);
const storeId = document.currentScript.getAttribute('storeId');
const brandId = document.currentScript.getAttribute('brandId');

ReactDOM.render(<App storeId={storeId} brandId={brandId} />, chatWidgetRoot);
