import React, { useState } from 'react';
import { useEffect } from 'react';
import { get } from 'lodash';
import { IInspiration } from '../../interfaces';
import instanceAxios from '../../utils/intanceAxios';

const StoryChatBubble = ({ left, id }) => {
  const [story, setStory] = useState<IInspiration>();

  useEffect(() => {
    let isMounted = true;
    instanceAxios
      .post('/api/inspirationsByIds', [id])
      .then((result) => {
        if (isMounted) {
          setStory(get(result, 'data[0]'));
        }
      })
      .catch((e) => {
        console.error(e);
      });
    return () => {
      isMounted = false;
    };
  }, []);

  if (!story) {
    return <span>Loading story...</span>;
  }

  const onClickStory = () => {
    console.log('click on story');
  };

  return (
    <div className={`message-content ${left && `left`}`} onClick={onClickStory}>
      <div className="item">
        <div className="story-info">
          <h4 className="story-title">{story.title}</h4>
        </div>
        <div className="story-image">
          <img src={story.flipImageUrl} />
        </div>
      </div>
      <style jsx>
        {`
          .message-content {
            width: 100%;
            cursor: pointer;
          }
          .item {
            background-color: #d6d6d6;
            padding: 10px;
          }
          .story-info {
            width: 100%;
            padding-right: 5px;
          }
          .story-title {
            font-size: 12px;
            font-weight: bold;
          }
          .story-image {
            max-width: 100%;
          }
          .story-image img {
            width: 100%;
          }
        `}
      </style>
    </div>
  );
};

export default StoryChatBubble;
