import { get } from 'lodash';
import React, { useEffect, useState } from 'react';
import { getProductsByIds } from '../../service';
import { IProduct } from '../../interfaces';

const ProductChatBubble = ({
  left,
  id,
  onClickProduct
}: {
  left: boolean;
  id: string;
  onClickProduct: (productId: string) => void;
}) => {
  const [product, setProduct] = useState<IProduct>();
  useEffect(() => {
    let isMounted = true;
    console.log('id: ', id);
    getProductsByIds([id])
      .then((result) => {
        if (isMounted) {
          setProduct(get(result, 'data[0]'));
        }
      })
      .catch((e) => {
        console.error(e);
      });

    return () => {
      isMounted = false;
    };
  }, []);

  if (!product) {
    return <span>Loading product...</span>;
  }

  let currentLanguage = 'EN';
  if (currentLanguage === 'JA') {
    currentLanguage = 'JP';
  }

  const productDescription = get(
    product,
    `watchSpecifications[${currentLanguage}].description`,
    ''
  );

  return (
    <div
      className={`message-content ${left && `left`}`}
      onClick={() => {
        onClickProduct(product.id);
      }}
    >
      <div className="item">
        <div className="product-info">
          <h3 className="product-title">{product.modelName}</h3>
          <h4 className="product-description">{productDescription}</h4>
        </div>
        <div className="product-image">
          <img src={product.previewImageUrl} />
        </div>
      </div>
      <style jsx>
        {`
          .message-content {
            width: 100%;
            cursor: pointer;
          }
          .item {
            display: flex;
            background-color: #d6d6d6;
            padding: 10px;
          }
          .product-info {
            width: 100%;
            padding-right: 5px;
          }
          .product-title {
            font-size: 14px;
            font-weight: bold;
          }
          .product-description {
            font-size: 12px;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          .product-image {
            max-width: 30%;
          }
          .product-image img {
            width: 100%;
          }
        `}
      </style>
    </div>
  );
};

export default ProductChatBubble;
